@import 'Variables';

.containerFavPrint {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}
.favHeader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
.favHeader__image {
    background: url('../images/pokemonLogo.png') no-repeat;
    background-size: contain;
    background-position: center center;
    height: 200px;
    transform: scale(1.4);
    width: 100%;
  }
.favHeader__text {
    margin: 15px;
    font-size: 20px;
    text-align: center;
    margin-bottom: 80px;
    margin-top: 40px;
  }
.favHeader__text--pokemon {
    text-transform: capitalize;
    color: $color-2;
    font-weight: bolder;
  }
.favPrint {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: scale(1.3);
  width: 280px;
  margin: 20px;
  padding: 40px;
  border: solid $color-2 5px;
  border-radius: 50px;
}

.favPrint__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 220px;
  margin-top: 30px;
  font-size: 16px;
}
.favPrint__text--username{
  color: $color-2;
  text-transform: capitalize;
  font-size: 30px;
  font-weight: 600;
  margin-top: 15px;
}
.favPrint__text--is{
  color: $color-3;
  font-size: 20px;
  margin-top: 10px;
}
.favPrint__text--favPoke {
  text-transform: capitalize;
  font-size: 25px;
  color: $color-1;
  margin-top: 10px;
}

// STAR SHAPE START
.favPrint__star {
  margin: 50px 0;
  position: relative;
  display: block;
  color: $color-1;
  width: 0px;
  height: 0px;
  border-right: 100px solid transparent;
  border-bottom: 70px solid $color-1;
  border-left: 100px solid transparent;
  transform: rotate(35deg);
  z-index: -1;
}
.favPrint__star:before {
  border-bottom: 80px solid $color-1;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  position: absolute;
  height: 0;
  width: 0;
  top: -45px;
  left: -65px;
  display: block;
  content: '';
  transform: rotate(-35deg);
  z-index: -1;
}
.favPrint__star:after {
  position: absolute;
  display: block;
  color: $color-1;
  top: 3px;
  left: -105px;
  width: 0px;
  height: 0px;
  border-right: 100px solid transparent;
  border-bottom: 70px solid $color-1;
  border-left: 100px solid transparent;
  transform: rotate(-70deg);
  content: '';
  z-index: -1;
}
// STAR SHAPED
.favPrint__star--image {
  width: 100px;
  transform: translateX(-50px) rotate(-35deg) scale(0.9);
}